define('asic-ui/initializers/inject-services', ['exports'], function (exports) {
  exports.initialize = initialize;

  function initialize(container, application) {
    application.inject('route', 'title', 'service:title');
    application.inject('component:single-article', 'highlighter', 'service:highlighter');
    application.inject('component:search-view', 'search', 'service:search');
  }
});