define('asic-ui/utils/preload-images', ['exports'], function (exports) {
  exports['default'] = preloadImages;

  function preloadImages(urls) {
    if (urls) {
      urls.forEach(function (url) {
        console.info('[imagePreload:] Preloading `%s`', url);
        new Image().src = url;
      });
    }
  }
});