        var el6 = dom.createTextNode("\n\n          ");
        dom.appendChild(el5, el6);
        var el6 = dom.createElement("p");
        var el7 = dom.createTextNode("\n            As a teenager, I was deeply interested in web\n            design.  With the exception of videogames, there\n            wasn't much else I spent time on.  In fact, one\n            interest served to reinforce the other in many ways.\n          ");
        dom.appendChild(el6, el7);
        dom.appendChild(el5, el6);
        var el6 = dom.createTextNode("\n\n          ");
