        dom.appendChild(el6, el7);
        var el7 = dom.createTextNode("\n            My earliest programming undertaking was born out of\n            the desire to correct several UI inefficiencies of a\n            Miva-scripted bulletin board application on a\n            videogame fan club website.  Achieving that goal and\n            receiving many accolades from my fellow gamers for\n            solving that long-lived problem, that curiosity\n            shifted to full-blown web design and development.");
        dom.appendChild(el6, el7);
        dom.appendChild(el5, el6);
        var el6 = dom.createTextNode("\n          ");
        dom.appendChild(el5, el6);
        var el6 = dom.createElement("p");
        var el7 = dom.createTextNode("\n            Armed with the momentum of my first coding victory,\n            I then went forth redesigning Geocities-quality HTML\n            3.0 websites to feature custom-made Photoshop\n            graphics, CSS and coherent color schemes.  Since the\n            clientele was invariably my fellow broke, unemployed\n            gaming teenage peers, I never charged anything for\n            designs and when I hosted others' fan pages, I did\n            so free of charge.  I did this right up until I\n            joined the service.\n          ");
        dom.appendChild(el6, el7);
