        var el6 = dom.createElement("p");
        var el7 = dom.createTextNode("\n            Normally, I take great pride in my online\n            hermitude—the policy of hyper-vigilant web\n            privacy and repression I have honed over the last\n            decade is rivaled only by folks on the run from the\n            law—and I wear it as a badge of honor in a\n            time where public over-sharing seems to be the norm.\n            That said, I do need a place to host technical\n            experimentation, lessons learned, sample data and my\n            portfolio.  I'll try to keep it professional!\n          ");
