/* jshint ignore:start */

define('asic-ui/config/environment', ['ember'], function(Ember) {
  var prefix = 'asic-ui';
/* jshint ignore:start */

try {
  var metaName = prefix + '/config/environment';
  var rawConfig = Ember['default'].$('meta[name="' + metaName + '"]').attr('content');
  var config = JSON.parse(unescape(rawConfig));

  return { 'default': config };
}
catch(err) {
  throw new Error('Could not read config from meta tag with name "' + metaName + '".');
}

/* jshint ignore:end */

});

if (!runningTests) {
  require("asic-ui/app")["default"].create({"name":"asic-ui","version":"2.3.0+076360c7"});
}

/* jshint ignore:end */
